import * as React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Table from 'react-bootstrap/Table'
import moment from 'jalali-moment'
import Modal from 'react-bootstrap/Modal'
import Print from '../pages/Print'
import { IconButton } from '@mui/material';
import LocalPrintshopRoundedIcon from '@mui/icons-material/LocalPrintshopRounded';
import Button from '@mui/material/Button';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import Tooltip from '@mui/material/Tooltip';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
const axios = require('axios').default;

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function DenseTable(state) {
  const [fullscreen, setFullscreen] = React.useState(true);
  const [show, setShow] = React.useState(false);
  const [vlc, setVlc] = React.useState(false);
  const [open, setOpen] = React.useState(false);
  const [opens, setOpens] = React.useState(false);
  const [user, setUser] = React.useState([]);
  const [Id, setId] = React.useState();
  const [select, setSelect] = React.useState(false);
  const [userNeme, setUserNeme] = React.useState();

  const [open2, setOpen2] = React.useState(false);

  const handleClose2 = () => {
    setOpen2(false);
  };


  const handleClose = () => {
    setOpen(false);
  };
  function handleShow(getid) {
    let getUser = state.state.filter(item => item._id === getid.id)
    setUser(getUser)
    setFullscreen(true);
    setShow(true);
  }
  if (state.state.length === 0) {
    return false
  }
  function handelremoeve() {
    RemovedItem(Id)
    setOpen(false)
    setOpens(true)
  }
  function separate(Number) {
    Number += '';
    Number = Number.replace(',', '');
    let x = Number.split('.');
    let y = x[0];
    let z = x.length > 1 ? '.' + x[1] : '';
    var rgx = /(\d+)(\d{3})/;
    while (rgx.test(y))
      y = y.replace(rgx, '$1' + ',' + '$2');
    return y + z;
  }
  function converReversDate(ymd) {
    let date = new Date(ymd).toLocaleDateString()
    return `${date.substring(6, 10)}/${date.substring(0, 2)}/${date.substring(3, 5)}`
  }
  function RemovedItem(getId) {
    axios.delete(`https://hoteldari.herokuapp.com/person/${getId.id}`)
      .then((respone) => {
        state.setDeletet(getId.id)
        setOpens(false)
      })
      .catch((err) => alert(err))
  }

  return (
    <div className='shadow rounded-2 my-5 p-2 bdkhflks'>
      <header className='tbl-how d-flex justify-content-between align-items-center bg-info p-1 rounded-2 text-bold '>
        <span className='p-2 s1-12' onClick={() => setOpen2(true)}>
          <LocalPrintshopRoundedIcon />
        </span>
        <span className='p-2 s2-12'>نام و نام خانودگی</span>
        <span className='p-2 s3-12'>شماره اتاق</span>
        <span className='p-2 s4-12'>شهر</span>
        <span className='p-2 s5-12'>تلفن</span>
        <span className='p-2'>تاریخ ورود</span>
        <span className='p-2'>تاریخ خروج</span>
        <span className='p-2'>تاریخ ثبت</span>
        <span className='p-2'>قیمت اتاق</span>
        <span className='p-2 s6-12'>مدت اقامت</span>
        <span className='p-2 s8-12'>تخفیف</span>
        <span className='p-2 s7-12'>قیمت کل</span>
      </header>
      <article className={`border-bottom  rounded ${state.rev ? "nhasoj" : ""}`}>
        {
          state.state.map((item, index) => (
            <div className='list-ts ' key={item._id} id={item._id} onDoubleClick={() => handleShow({ id: item._id })}>
              <Tooltip className='cfd' title="برای حذف کردن روی هر شماره هر لیست کلیک کنید" placement="right-start">
                <span className='s1-12' onClick={() => {
                  setOpen(true)
                  setId({ id: item._id })
                  setUserNeme(item.name)
                }} >{index + 1}</span>
              </Tooltip>
              <span className='s2-12'>{item.name}</span>
              <span className='s3-12'>{item.number}</span>
              <span className='s4-12'>{item.address}</span>
              <span className='s5-12'>{item.phone}</span>
              <span>{item.dateArrival}</span>
              <span>{item.dateExport}</span>
              <span className='text-bold'>{
                moment(converReversDate(item.curentDate), 'YYYY/MM/DD').locale('fa').format('YYYY/MM/DD')
              }</span>
              <span>{separate(item.typeRoom)}</span>
              <span className='s6-12'>{item.lengthDay}</span>
              <span className='s8-12'>{item.discount + "%"}</span>
              <span className='s7-12'>{separate(item.totalPrice) + " تومان"}</span>
            </div>
          ))
        }
      </article>
      <Modal className="modal-s" show={show} fullscreen={fullscreen} onHide={() => setShow(false)}>
        <Modal.Header closeButton>
          <Modal.Title>مهمانسرا دانشگاه شمال</Modal.Title>
          <IconButton color="primary" >
            <LocalPrintshopRoundedIcon color="primary" style={{ visibility: vlc ? "hidden" : "inherit" }} onClick={(e) => {
              window.print()
            }
            } />
          </IconButton>
        </Modal.Header>
        <Modal.Body>
          <Print user={user} />
        </Modal.Body>
      </Modal>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {`آیا میخواهید مهمان به نام ${userNeme} را از لیست حذف کنید!`}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            مهمان حذف شده به طور کامل از دیتابیس حذف می شود
            و غیر قابل بر گشت است
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            className='text-white bg-primary'
            onClick={handleClose}
          >لغو</Button>
          <Button className='text-white bg-danger' onClick={handelremoeve} autoFocus>
            تایید
          </Button>
        </DialogActions>
      </Dialog>
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={opens}
      >
        <CircularProgress />
      </Backdrop>
      <Dialog
        fullScreen
        open={open2}
        onClose={handleClose}
        TransitionComponent={Transition}
        className='sdsdsdssdfdsf'
      >
        <div className="bg-info d-flex p-3 align-items-center">
          <IconButton onClick={handleClose2}>
            <CloseIcon className='text-white' />
          </IconButton>
          <IconButton onClick={() => window.print()}>
            <LocalPrintshopRoundedIcon className='text-white' />
          </IconButton>
          <h4 className='me-5'>{state.state.length} مهمان</h4>
        </div>

        <header className='tbl-how d-flex justify-content-between align-items-center bg-info p-1 rounded-2 text-bold sdfsdfwe'>
          <span className='p-2 s1-12 d-flex' onClick={() => setOpen2(true)}>
            #
          </span>
          <span className='p-2 s2-12'>نام و نام خانودگی</span>
          <span className='p-2 s3-12'>شماره اتاق</span>
          <span className='p-2 s4-12'>شهر</span>
          <span className='p-2 s5-12'>تلفن</span>
          <span className='p-2'>تاریخ ورود</span>
          <span className='p-2'>تاریخ خروج</span>
          <span className='p-2'>قیمت اتاق</span>
          <span className='p-2 s6-12'>مدت اقامت</span>
          <span className='p-2 s8-12 w-0-f'>تخفیف</span>
          <span className='p-2 s7-12'>قیمت کل</span>
        </header>

        <article className={`border-bottom  rounded`}>
          {
            state.state.map((item, index) => (
              <div className='list-ts fdfddd' key={item._id}>
                <span className='s1-12'>{index + 1}</span>
                <span className='s2-12'>{item.name}</span>
                <span className='s3-12'>{item.number}</span>
                <span className='s4-12'>{item.address}</span>
                <span className='s5-12'>{item.phone}</span>
                <span>{item.dateArrival}</span>
                <span>{item.dateExport}</span>
                <span>{separate(item.typeRoom)}</span>
                <span className='s6-12'>{item.lengthDay}</span>
                <span className='s8-12 w-0-f'>{item.discount + "%"}</span>
                <span className='s7-12'>{separate(item.totalPrice) + " تومان"}</span>
              </div>
            ))
          }
        </article>
        <div className="d-flex justify-content-center align-items-center pt-5"><h3 className='pt-5'>جمع کل: {separate(state.sum)} تومان</h3></div>
      </Dialog>
    </div>
  );
}
